.App {
  text-align: center;
  background-color: aqua;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.navbar{
  background-color: red;
  height: 100px;
  width: auto;
  text-align: left;
  margin: 0;
  position: fixed;
  width: 100%;
  display: block;
}

.hero-section {
  padding-top: 100px;
  background-color: blue;
  height: 300px;
  display: grid;
  grid-template-columns: 1fr 1fr; /* This line splits the grid into two equal columns */
}

.hero1{
  background-color: yellow;
}
.hero2{
  background-color: pink;
  width: 100%;
  height: 100%;
}

.img-hero{
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.form{
  margin: 100px;
}

.myform{
  padding-top: 110px;
}
.feild{
  height: 20px;
  border-radius: 10px;
}